import React from "react";

const getViewBox = name => {
  switch (name) {
    case "warning":
      return "0 0 20 20";
    case "pen":
      return "0 0 28 28";
    case "globe":
      return "0 0 30 30";
    case "bubble":
      return "0 0 20 20";
    case "handwriting":
      return "0 0 80 80";
    case "people":
    case "horloge":
    case "papiers":
    case "carte":
    case "frBubble":
      return "0 0 25 25";
    case "construction":
      return "0 0 40 40";
    case "calendar" || "candle":
      return "0 0 20 20";
    case "papers":
      return "0 0 21 20";
    case "clapping":
      return "0 0 114 114";
    case "translate":
      return "0 0 25 25";
    case "idea":
      return "0 0 100 100";
    case "question":
      return "0 0 100 100";
    case "paragraph":
      return "0 0 20 20";
    case "bubbleTranslate":
      return "0 0 17 16";
    case "money":
      return "0 0 18 18"
    case "fire":
      return "0 0 20 20";
    default:
      return "0 0 20 20";
  }
};

const getFill = name => {
  switch (name) {
    case "people" || "horloge" || "papiers" || "carte" || "frBubble":
      return "#3D3D3D";
    case "construction":
      return "black";
    case "papers":
      return "none";
    case "clapping":
      return "none";
    case "idea" || "question":
      return "#3D3D3D";
    default:
      return "";
  }
};

